import React, { Children, useRef, useEffect } from 'react';
import { Icon } from 'react-materialize'
import cx from 'classnames';
import M from 'materialize-css'

        // className={cx("blue darken-2", { pulse: cartSize })}
        // fab={{ direction: "top", hoverEnabled: false }}
        // floating
        // large
        // icon={cartSize ? <>
        //     <AddedProductsIndicator>{cartSize}</AddedProductsIndicator>
        //     <CustomIcon style={disabledTextSelectionStyle}>shopping_cart</CustomIcon>
        // </> : <CustomIcon style={disabledTextSelectionStyle} className={"notranslate"}>shopping_cart</CustomIcon>}
        // node="button"

const CustomButton = ({
    children,
    className,
    node,
    fab,
    modal,
    flat,
    floating,
    large,
    small,
    disabled,
    waves,
    tooltip,
    tooltipOptions = {},
    icon,
    ...props
}) => {
    const _btn = useRef(null);
    const _fab = useRef(null);

    useEffect(() => {
        if (tooltip) {
            const instance = M.Tooltip.init(_btn.current, tooltipOptions);

            return () => {
                instance && instance.destroy();
            };
        }
    }, [tooltip, tooltipOptions, children]);

    useEffect(() => {
        if (fab && _fab.current) {
            const instance = M.FloatingActionButton.init(_fab.current, fab);

            return () => {
                instance && instance.destroy();
            };
        }
    }, [fab]);

    const renderFab = classes => {
        return (
            <div
                ref={_fab}
                className={cx('fixed-action-btn', {
                    toolbar: fab.toolbarEnabled
                })}
            >
                <div className={classes}>{icon || <Icon>edit</Icon>}</div>
                <ul>
                    {Children.map(children, child => (
                        <li>{child}</li>
                    ))}
                </ul>
            </div>
        );
    };

    let C = node;
    let classes = {
        btn: true,
        disabled,
        'waves-effect': waves
    };

    if (modal) {
        classes['modal-' + modal] = true;
    }

    if (fab) {
        return renderFab(cx(classes, className));
    } else {
        return (
            <C
                {...props}
                disabled={disabled}
                className={cx(classes, className)}
                ref={_btn}
                data-tooltip={tooltip}
            >
                {icon}
                {children}
            </C>
        );
    }
};

CustomButton.defaultProps = {
    node: 'button'
};

export default CustomButton;
